.contacts__info-list {
  list-style: none;
  padding: 0;

  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
}
.contacts__info-title {
  margin: 10px 0;

}
.contacts__info-text {
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
}
.contacts__map {
  width: 100%;
  margin-bottom: 50px;
}
.contacts__map-container{
  margin: 0 auto;
  max-width: 1200px;

}
.contacts__author{
  margin-bottom: 100px;
}

@media screen and (max-width: 1920px) {
  .contacts__info-list {
    margin-bottom: 85px;
  }

  .contacts__info-text {
    font-size: 22px;
    line-height: 25px;
  }
  .contacts__map {
    height: 450px;
    margin-bottom: 38px;
  }
  .contacts__map-container{
    max-width: 66%;
  }
  .contacts__author{
    margin-bottom: 75px;
  }
}

@media screen and (max-width: 1440px) {
  .contacts__map {
    height: 374px;
    margin-bottom: 32px;
  }
  .contacts__author{
    margin-bottom: 62px;
  }
}
@media screen and (max-width: 1200px) {
  .contacts-page .header-component{
    margin-bottom: 0;
  }
  .contacts__info-list {
    margin-bottom: 20px;
    flex-direction: column;
  }
  .contacts__info-item{
    margin-bottom: 30px;
  }
  .contacts__map-container{
     max-width: 100%;
   }
  .contacts__map {
    height: 374px;
    margin-bottom: 32px;
  }
}
@media screen and (max-width: 768px) {
  .contacts__info-title{
    font-size: 12px;
    line-height: 15px;
  }
  .contacts__info-text {
    font-size: 14px;
    line-height: 17px;
  }
  .contacts__author{
    font-size: 9px;
    line-height: 11px;
  }
}

