.nav-list{
  font-size: 14px;
  line-height: 19px;

  display: flex;
  justify-content: space-between;

  list-style: none;
  padding: 0;
  width: 600px;

  margin: 0 103px 0 0;

  a{
    color: #00456d;
    text-decoration: none;
  }

}

.header-component .nav-list{
    max-width: 500px;
}
.nav-list__item{
  position: relative;
}
.nav-list__item a:hover,
.nav-list__item--active a{
  text-shadow: 0 0 .65px #00456d, 0 0 .65px #00456d;
}
.nav-list__item a:hover::before,
.nav-list__item a:active::before,
.nav-list__item a:focus::before,
.nav-list__item--active a::before{
  content: '';
  width: 5px;
  height: 5px;
  position: absolute;
  background-color: #FF8767;
  right: -10px;
  bottom: 5px;
}
@media screen and (max-width: 1920px) {
  .nav-list{
  margin: 0 70px 0 0;
  }
}

@media screen and (max-width: 1440px) {
  .nav-list{
    width: 460px;
    margin: 0 40px 0 0;
  }
}

@media screen and (max-width: 1200px) {

  .header-component .nav-list{
    max-width: none;
    z-index: 1;
  }

  .nav-list{
    z-index: 10;
    width: 100%;

    flex-direction: column;
    align-items: center;
    left: 0%;
    position: absolute;

    top: 75px;

    transition: all 0.5s ease 0s;

    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(25px);
  }
  .nav-list--active{
    left: 0%;

  }
  .nav-list--disable{
    left: -150%;
  }
  .nav-list__item{
    width: 100%;
  }
  .nav-list__item a{
    display: block;
    text-align: center;
    vertical-align: middle;
    padding: 11px 0;
    height: 100%;
    width: 100%;
  }
  .nav-list__item a:hover,
  .nav-list__item a:active,
  .nav-list__item a:focus,
  .nav-list__item--active a{
    backdrop-filter: blur(25px);

    &::before{
      right: 44%;
      bottom: 15px;
    }
  }
}
@media screen and (max-width: 768px) {
  .nav-list{
    top: 100%;
  }
  .nav-list__item a{
    padding: 11px 0;
  }
  .nav-list__item a:hover,
  .nav-list__item a:active,
  .nav-list__item a:focus,
  .nav-list__item--active a{
    &::before{
      right: 30%;
    }
  }
}
