*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  min-height: 100%;
  position: relative;

  font-family: Proxima Nova,Arial, sans-serif;
  font-size: 18px;
  line-height: 25px;
  color: #00456D;
}
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
.btn{
  padding: 9px 44px;
  border: 1px solid #FF8767;
  text-decoration: none;

  font-family: Proxima Nova,Arial, sans-serif;
  font-size: 14px;
  line-height: 29px;
  text-align: center;
  text-transform: uppercase;
}
ul.list--hidden{
  display: none;
}

.container{
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
}
.container-min{
  max-width: 1200px;
  margin: 0 auto;
}
.products .container-min{
  max-width: 1275px;
}
@media screen and (max-width: 1920px) {
  .container{
    margin: 0 auto;
  }
  .container-min{
    max-width: 66%;
    margin: 0 auto;
  }
}


@media screen and (max-width: 1440px) {
  .container-min{
    margin: 0 auto;
  }
}

@media screen and (max-width: 1200px) {
  .container{
    margin: 0 auto;
  }
  .container-min{
    max-width:64.4%;
    margin: 0 auto;
  }

}
@media screen and (max-width: 768px) {
  .container{
    margin: 0 auto;
  }
  .container-min{
    max-width: 63.8%;
    margin: 0 auto;
  }
}

