.our-goal {
  &__wrapper {
    background-image: url("../img/about-us.jpg");
    background-position: left top;
    background-repeat: no-repeat;
    background-size: 960px 100%;
    margin-bottom: 150px;
    padding-top: -1px;
    margin-top: -1px;
    padding-bottom: 100px;
  }
}

.our-goal__card {
  width: 850px;
  margin-left: auto;
  margin-right: 470px;

  margin-top: 0;

  padding: 60px 0 60px 180px;

  &-title {
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    text-transform: uppercase;

    margin-bottom: 20px;
  }

  &-text {
    margin-bottom: 100px;
  }
}


@media screen and (max-width: 1920px) {
  .our-goal {
    &__wrapper {
      background-size: 50% 100%;
      margin-bottom: 113px;
      padding-bottom: 75px;
    }
  }
  .our-goal__card {
    width: 44.3%;
    margin-right: 24.5%;

    padding: 45px 0 45px 135px;

    &-title {
      font-size: 27px;
      line-height: 36px;
      margin-bottom: 15px;
    }

    &-text {
      margin-bottom: 75px;
    }
  }
}

@media screen and (max-width: 1440px) {
  .our-goal {
    &__wrapper {
      background-size: 598px 100%;
      margin-bottom: 94px;
      padding-bottom: 62px;
    }
  }
  .our-goal__card {
    width: 44.1%;
    margin-right: 19.9%;

    padding: 37px 0 37px 112px;

    &-title {
      font-size: 22px;
      line-height: 27px;
      margin-bottom: 10px;
    }

    &-text {
      margin-bottom: 62px;
    }
  }
}
@media screen and (max-width: 1200px) {
  .our-goal {
    margin-top: 150px;
    &__wrapper {
      background-size: 100% 561px;
      margin-bottom: 94px;
      padding-bottom: 62px;
    }
  }
  .our-goal__card {
    position: relative;
    top: -180px;
    width: 644px;
    margin-left: auto;
    margin-right: auto;
    //padding-bottom: 76px;
    //margin-top: 254px;
    padding: 30px 75px 75px;

    //width: 44.1%;
    //margin-right: 19.9%;
    //
    //padding: 37px 0 37px 112px;

    &-title {
      font-size: 22px;
      line-height: 27px;
      margin-bottom: 10px;
    }

    &-text {
      margin-bottom: 62px;
    }
  }
}
@media screen and (max-width: 768px) {
  .our-goal {
    margin-top: 50px;
    &__wrapper {
      background-image: url("../img/about-us-m.jpg");
      background-size: 100% 263px;
      margin-bottom: 94px;
      padding-bottom: 0;
    }
  }
  .our-goal__card {
    position: relative;
    top: -110px;
    width: 83.8%;
    margin-left: auto;
    margin-right: auto;
    padding: 23px 35px 36px;

    &-title {
      font-size: 10px;
      line-height: 12px;
      margin-bottom: 10px;
    }

    &-text {
      font-size: 10px;
      line-height: 12px;
      margin-bottom: 30px;
    }
  }
}





