.activities{
  .section-title__wrapper{
    max-width: 750px;
  }
}
.activities__list {
  list-style: none;
  padding: 0;

  margin-bottom: 200px;
}

.activities__item {
  display: flex;

  //align-items: flex-start;
}

.activities__item:not(:last-child) {
  margin-bottom: 100px;
}

.activities img {
  width: 50%;
  align-self: stretch;
}
.activities__wrapper {
  position: relative;
  z-index: 1;
  padding: 86px 50px 50px 50px;
  margin-bottom: 50px;

}

// нечетные
.activities__item:nth-child(odd) {
  .activities__wrapper{
    left: -120px;
    padding-left: 50px;
  }
}

// четные
.activities__item:nth-child(even) {
  img {
    margin-left: auto;
  }

  .activities__wrapper {
    right: -120px;
    text-align: right;
    padding-right: 50px;
    order: -1;
  }

  .activities__title,
  .activities__sub-title {
    margin-left: auto;
  }

}

.activities__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;

  margin-bottom: 30px;
}

.activities__sub-title {
  width: 570px;
}


@media screen and (max-width: 1920px) {
  .activities__list {
    margin-bottom: 150px;
  }
  .activities__wrapper{
    padding-top:65px  ;
    margin-bottom: 38px;
  }
  .activities__item:not(:last-child) {
    margin-bottom: 75px;
  }
  // нечетные
  .activities__item:nth-child(odd) {
    .activities__wrapper {
      left: -10%;
      padding-left: 38px;

    }
  }

  // четные
  .activities__item:nth-child(even) {

    .activities__wrapper {
      right: -10%;
      padding-right: 38px;
    }
  }

}

@media screen and (max-width: 1440px) {
  .activities__list {
    margin-bottom: 125px;
  }
  .activities__item:not(:last-child) {
    margin-bottom: 62px;
  }
  .activities__wrapper{
    margin-bottom: 32px;
  }

  // нечетные
  .activities__item:nth-child(odd) {
    .card__blur {
      padding-left: 32px;
    }
  }

  // четные
  .activities__item:nth-child(even) {
    .card__blur {
      padding-right: 32px;
    }

  }
  .activities__title {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 10px;
    width: 350px;
  }
  .activities__sub-title {
    font-size: 14px;
    line-height: 17px;
    width: 460px;
  }

}
@media screen and (max-width: 1200px) {
  .activities__list {
    margin-bottom: 125px;
  }
  .activities__wrapper {
      margin-bottom: 30px;
      padding-top: 20px;
      padding-bottom: 20px;
  }
  .activities__item:not(:last-child) {
    margin-bottom: 62px;
  }

  // нечетные
  .activities__item:nth-child(odd) {
    //.activities__wrapper{
    //  left: 50%;
    //}
    .card__blur {
      padding-left: 32px;
    }
  }

  // четные
  .activities__item:nth-child(even) {
    //.card__blur {
    //  padding-right: 32px;
    //}

  }
  .activities__title {
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 10px;
    max-width:300px;
  }

  .activities__sub-title {
    font-size: 10px;
    line-height: 15px;
    max-width:300px;
  }
}
@media screen and (max-width: 768px) {
  .activities .container-min{
    max-width: none;
  }
  .activities__list {
    margin-bottom:80px;
  }
  .activities__wrapper {
    padding: 20px;
    margin-bottom: 22px;
    max-width: 60%;
  }
  .activities__item:not(:last-child) {
    margin-bottom: 42px;
  }


  // нечетные
  .activities__item:nth-child(odd) {
    //.activities__wrapper{
    //  left: 50%;
    //}
    .card__blur {
      padding-left: 22px;
    }
  }

  // четные
  .activities__item:nth-child(even) {
    .card__blur {
      padding-right: 22px;
    }
    img{
      margin-left: -10%;
    }

  }
  .activities__title {
    font-size: 10px;
    line-height: 12px;
    margin-bottom: 10px;
    max-width:100%;
  }

  .activities__sub-title {
    font-size: 9px;
    line-height: 11px;
    width: auto;
    max-width:220px;
  }
}

