.our-values__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 200px;
}

.our-values__item {
  width: 25%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
}

.our-values__item:not(:nth-child(3n)) {
  margin-right: 40px;
}

.our-values__item:not(:nth-last-child(-n+3)) {
  margin-bottom: 72px;
}
.our-values__item:nth-child(2) .our-values__svg{
  margin-bottom: 15px;
}


.our-values__item-title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  text-transform: uppercase;
  max-width: 220px;
  min-height: 71px;
}

.our-values__svg {
  height: auto;
  margin-bottom: 30px;
}

.our-values__svg--team {
  width: 32%;
}

.our-values__svg--human_relations {
  width: 37%;
}

.our-values__svg--competence {
  width: 32%;
}

.our-values__svg--efficiency {
  width: 30%;
}

.our-values__svg--safety {
  width: 32%;
}

.our-values__svg--responsibility {
  width: 33%;
}

@media screen and(max-width: 1920px) {
  .our-values__list {
    margin-bottom: 150px;
  }
  .our-values__item {
    width: 25%;
    font-weight: normal;
    font-size: 15px;
    line-height: 22px;
  }
  .our-values__item:not(:nth-child(3n)) {
    margin-right: 30px;
  }
  .our-values__item:not(:nth-last-child(-n+3)) {
    margin-bottom: 54px;

  }
  .our-values__item-title {
    font-size: 18px;
    line-height: 27px;
    max-width: 165px;
    min-height: 71px;
  }
  .our-values__svg {
    margin-bottom: 25px;
  }
  .our-values__item:nth-child(2) .our-values__svg{
    margin-bottom: 15px;
  }
}

@media screen and(max-width: 1200px) {
  .our-values__list {
    margin-bottom: 150px;
  }
  .our-values__item {
    width: 45%;
    font-weight: normal;
    font-size: 12px;
    line-height: 22px;
    align-items: center;
    text-align: center;
  }
  .our-values__list .our-values__item {
    margin-right: 0;
    margin-bottom: 54px;

  }
  .our-values__item-title {
    margin-top: 15px;
    font-size: 18px;
    line-height: 27px;
    max-width: 165px;
    min-height: 71px;
  }
  .our-values__svg {
    margin-bottom: auto;
  }
  .our-values__item:nth-last-child(2) .our-values__svg{
    margin-bottom: 0;
  }

}

@media screen and(max-width: 768px) {
  .our-values .section-title__header {
    margin-bottom: 50px;
  }
  .our-values__list {

    margin-bottom: 80px;
  }
  .our-values__item {
    width: 100%;
    font-size: 12px;
    line-height: 15px;

  }
  .our-values__list .our-values__item {
    margin-right: 0;
  }
  .our-values__item:not(:last-child) {
    margin-bottom: 50px;

  }
  .our-values__item-title {
    font-size: 18px;
    line-height: 27px;
    max-width: 165px;
    min-height: auto;
  }
  .our-values__svg {
    margin-bottom: 15px;
  }
}
