.vendors{
  margin-bottom: 200px;
}
.vendors__wrapper {
  display: flex;
  flex-direction: column;
}

.vendors__list {
  list-style: none;
  padding: 0;
  margin: 0 0 70px;

  display: flex;
  flex-wrap: wrap;

  overflow: hidden;

}

.vendors__item {
  width: 25%;
  height: 300px;
  display: flex;
  opacity: 50%;
  filter: grayscale(100%);
  transition: filter 500ms ease, opacity 500ms ease ;
}
.vendors__item:hover{
  opacity: 100%;
  filter: none;
}

.vendors__item {
  position: relative;
  border-bottom: 1px solid #003056;
  border-right: 1px solid #003056;
  bottom: -2px;
  right: -2px;
}
.vendors__item a{
  display: flex;
  width: 100%;
  height: 100%;
}

