.vendors__link {
  margin-left: auto;

  &::before {
    top: 10px;
  }
}

.vendors picture {
  width: 300px;
  object-fit: cover;
  margin: auto;
}

.vendors img {
  object-fit: cover;
  height: 100%;
  width: 100%;
  margin: auto;
}

