@media screen and (max-width: 1920px) {
  .vendors{
    margin-bottom: 150px;
  }

  .vendors__list {
    margin-bottom:53px;
  }

  .vendors__item {
    height: 225px;
  }
  .vendors__link::before{
    top: 15px;
  }
}


@media screen and (max-width: 1440px) {
  .vendors{
    margin-bottom: 125px;
  }
  .vendors__list {
    margin-bottom:44px;
  }
  .vendors__item {
    height: 187px;
  }
  .vendors__link::before{
    top: 12px;
  }

}
@media screen and (max-width: 1200px) {

  .vendors__item{
    width: calc(100% / 3);
  }
}

@media screen and (max-width: 768px) {
  .vendors__item{
    width: 50%;
    height: 115px;
  }
  .vendors .vendors__item{
    border: 0.5px solid #00456D;
  }

  .vendors__item:nth-child(2n){
    border-right: none;
  }
  .vendors__item:nth-child(2n+1) {
    border-left: none;
    }
  .vendors__item:not(:nth-child(-n+2)),
  .vendors__item:not(:nth-last-child(-n+2)){
    border-left: none;
  }
  .vendors__item:nth-child(-n+2),
  .vendors__item:nth-last-child(-n+2) {
    border-top: none;
    border-bottom: none;
    border-left: none;
  }
  .vendors-page .vendors__item:nth-last-child(1){
    border:none ;
  }
}
