.news-item {
  margin-bottom: 200px;
}
.news-item__wrapper{
  background-image: url("../img/news-item-img.jpg");
  background-repeat: no-repeat;
  background-position: left top;
}
.news-item__time{
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;

  display: block;
  margin-bottom: 36px;
}
.news-item .news-item__card {
  width: 1273px;
  margin-left: auto;
  padding: 78px 0 68px 180px;
  &-title {
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    margin-bottom: 50px;
    span{
      display: block;
    }
  }
  &-text--top {
    margin-bottom: 50px;
    max-width: 540px;
  }
  &-text--bottom {
    position: relative;
    top: -85px;
    max-width: 600px;
    margin-left: auto;
    margin-right: 200px;
  }
}
.news-item__section{
  margin-bottom: 100px;
}
.news-item__sub-title{
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  margin-bottom: 30px;
  span{
    display: block;
  }
}
.news-item__sub-title--small{
  font-weight: 300;
}
.news-item__text-container{
  display: flex;
}
.news-item__left-side{
  margin-right: 80px;
}


.news-item__img-container{
  background-image: url("../img/activities_1.jpg");
  background-repeat: no-repeat;
  background-position: left top;
  background-size: 50% 76%;
  min-height: 437px;
}
.news-item .news-item__img-wrapper{
  margin-left: 470px;
  padding: 54px 50px 135px 50px;
}
.news-item__img-text{
  position: relative;
  top: -135px;
  width: 530px;
  margin-left: auto;
}

.news-item__list{
  margin-right: 95px;
  padding-left: 16px;
}
.news-item__item{
  margin-bottom: 20px;
}
@media screen and (max-width: 1920px) {

  .news-item {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 150px;
  }
  .news-item__wrapper {
    background-size: 50% 100%;;
    margin-bottom: 75px;
  }
  .news-item__time{
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 27px;
  }
  .news-item .news-item__card {
    width: 44.3%;
    margin-right: 20%;
    padding: 60px 0 50px 135px;

    &-title {
      font-size: 18px;
      line-height: 25px;
      margin-bottom: 38px;
    }
    &-text--top {
      margin-bottom: 38px;
    }
    &-text--bottom {
      margin-right: 5%;
    }
  }

  .news-item__section{
    margin-bottom: 75px;
  }
  .news-item__sub-title{
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 23px;
  }
  .news-item__left-side{
    margin-right: 60px;
  }

  .news-item__img-container{
    min-height: 328px;
  }
  .news-item .news-item__img-wrapper{
    margin-left: 353px;
    padding: 39px 38px 100px 38px;
  }
  .news-item__img-text{
    top: -100px;
    width: 440px;
  }
}

@media screen and (max-width: 1440px) {
   .news-item {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 125px;
  }
  .news-item__wrapper {
    margin-bottom: 62px;
  }
  .news-item__time{
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 22px;
  }
  .news-item .news-item__card {
    padding: 30px 0 30px 50px;

    &-title {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 15px;
    }
    &-text--top {
      margin-bottom: 20px;
    }
    &-text--bottom {
      top: -40px;
      max-width: 520px;
    }
  }

  .news-item__section{
    margin-bottom: 62px;
  }
  .news-item__left-side{
    margin-right: 50px;
  }

  .news-item__img-container{
    background-size: 50% 76%;
  }
  .news-item .news-item__img-wrapper{
    margin-left: 43%;
  }
  .news-item__img-text{
    width: 46%;
  }
}

@media screen and (max-width: 1200px) {
  .news-item-page .container-min--img{
    max-width: none;
  }
  .news-item {
    margin-top: 250px;
  }
  .news-item__wrapper {
    background-size: 100%;
    margin-bottom: 130px;
  }
  .news-item .news-item__card {
    width: auto;
    max-width: 644px;
    margin-left: auto;
    margin-right: auto;
    padding: 50px 75px 25px;
    top: -170px;
    position: relative;
    &-text--top {
      max-width: 540px;
    }
    &-text--bottom {
      position: relative;
      top: 70px;
      max-width:644px;
      margin-left: auto;
      margin-right: auto;
      padding: 50px 75px 0;
      width: auto;
      background: rgba(255,255,255,.5);
      backdrop-filter: blur(25px);
    }
  }


  .news-item__text-container{
    flex-direction: column;
  }
  .news-item__sub-title{
    line-height: 22px;
    margin-bottom: 19px;
  }

  .news-item__img-container{
    background-size: 100%;
    min-height: 0;
    margin-bottom: 80px;
  }
  .news-item__img-text-container{
    background: rgba(255,255,255,.5);
    backdrop-filter: blur(25px);
    max-width: 644px;
    margin-left: auto;
    margin-right: auto;
    padding: 50px 75px 25px;
    position: relative;
    top: -110px;
    margin-top: 150px;
  }
  .news-item .news-item__img-wrapper{
    padding: 0;
    margin-left: 0;
    background: none;
    backdrop-filter: blur(0);
  }
  .news-item__img-text{
    position: static;
    width: 100%;
  }

  .news-item__list{
    margin-right: 0;
    padding-left: 16px;
  }
  .news-item__item{
    margin-bottom: 20px;
  }

}
@media screen and (max-width: 768px) {
  .news-item {
    font-size: 9px;
    line-height: 11px;
    margin-bottom: 80px;
    margin-top: 100px;
    &__wrapper {
      background-size: 100% 100%;
    }
  }
  .news-item__time{
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 0;
  }
  .news-item .news-item__card {
    width: 83.8%;
    padding: 23px 35px 36px;
    top: -80px;
    span{
      display: initial;
    }
    &-title {
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 10px;
    }
    &-text--top {
      margin-bottom: 0;
    }
    &-text--bottom {
      top: 80px;
      width: 83.8%;
      padding: 23px 35px 36px;
    }
  }
  .news-item__section{
    margin-bottom: 62px;
  }
}


