.products-item-page {
  .main {
    margin-bottom: 86px;
  }
  .products__wrapper{
    display: flex;
    flex-direction: column;
  }
  .products-item-link {
    margin-bottom: 100px;
  }
  .products__item--only {
    width: 100%;
    margin-bottom: 70px;
  }
  .products__item-top {
    margin-bottom: 50px;
  }
  .products__svg {
    width: 177px;
  }
  .products__item-title {
    font-size: 52px;
    line-height: 52px;
  }
}

@media screen and(max-width: 1200px) {
  .products-item-page {
    .main{
      margin-bottom: 66px;
    }

    .products-item-link {
      margin-bottom: 60px;
      padding-left: 60px;
    }

    .products__item--only {
      margin-bottom: 50px;
    }

    .products__item-top {
      margin-bottom: 40px;
      flex-direction: column;
    }
    .products__svg {
      width: 147px;
      margin-bottom: 25px;
    }
    .products__item-title {
      font-size: 36px;
      line-height: 36px;
    }
  }
}

@media screen and(max-width: 768px) {
  .products-item-page {
    .main {
      margin-bottom: 30px;
    }

    .products-item-link {
      margin-bottom: 30px;
      padding-left: 40px;
    }

    .products__item--only {
      margin-bottom: 30px;
    }

    .products__item-top {
      margin-bottom: 20px;
    }

    .products__svg {
      width: 120px;
    }
    .products__item-title {
      font-size: 26px;
      line-height: 26px;
    }
    .products__item-text {
      font-size: 14px;
      line-height: 22px;
    }
  }
}
