.modal-back{
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;


}
.form.modal{
  position: fixed;
  left: 50%;
  top: 30%;
  transform: translate( -50%, -50%);
  padding-left: 100px;
  width: 800px;
  animation-duration: .75s;
  z-index: 11;
  animation-name: fadeIn;
  background: rgba(255,255,255,0.8);
}
.modal__disable{
  display: none;

}
.modal__title{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.modal__close{
  cursor: pointer;
  font-size: 0;
  position: absolute;
  top: 15px;
  right: 45px;
  background-color: transparent;
  border: none;
  width: 25px;
  height: 25px;
  padding: 0;

  &::before,
  &::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #00456d;
    left:0;
  }
  &::before{
    top: 12px;
    transform: rotate(45deg);
  }
  &::after{
    bottom: 11px;
    transform: rotate(-45deg);
  }
}

@keyframes bounceInModal {
  0%, 20%, 40%, 60%, 80%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1)
  }
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3) translate( -50%, -50%)
  }
  20% {
    transform: scale3d(1.1, 1.1, 1.1) translate( -50%, -50%)
  }
  40% {
    transform: scale3d(0.9, 0.9, 0.9) translate( -50%, -50%)
  }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03) translate( -50%, -50%)
  }
  80% {
    transform: scale3d(0.97, 0.97, 0.97) translate( -50%, -50%)
  }
  to {
    opacity: 1;
    transform: scaleX(1) translate( -50%, -50%)
  }
}

@media screen and (max-width: 1920px) {
  .form.modal{
    width: 700px;
    padding: 35px 70px;
  }
}
@media screen and (max-width: 1440px) {
  .form.modal{
    width: 600px;
    padding: 25px 50px;
  }
}
@media screen and (max-width: 1200px) {
  .form.modal{
    width: 500px;
    padding: 25px 50px;
  }
}
@media screen and (max-width: 768px) {
  .form.modal{
    width: 500px;
    padding: 25px 20px;
  }
  .modal__close{
    top: 15px;
    right: 20px;
    width: 15px;
    height: 15px;
    &::before{
      top: 5px;
    }
    &::after{
      bottom: 8px;
    }
  }
}

