@keyframes beforeAnimation {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    visibility: visible
  }
  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
  }
}

.header-component {
  margin-top: 50px;
  margin-bottom: 150px;

  &__left-side{
    align-self: flex-end;
    margin-left: 411px;
    margin-bottom: 95px;
  }
  &__title {
    margin-right: auto;
    margin-bottom: 20px;

    font-weight: 300;
    font-size: 24px;
    line-height: 52px;
    text-transform: uppercase;

    animation-name: slideInLeft;

    span {
      display: block;
    }

    &--big {
      font-weight: 700;
      font-size: 52px;
    }
  }

  &__img {
    width: 50%;
  }

  &__bottom-side {
    display: flex;
    justify-content: space-between;

    position: relative;

    &::before {
      content: '';
      display: block;
      background-color: #ff8767;
      width: 100%;
      height: 85px;
      position: absolute;
      left: -52%;
      bottom: 0;
      animation-duration: 1s;
      animation-name: beforeAnimation;
      animation-timing-function: ease;
    }
  }

  .main-nav {
    margin-top: 5px;
    margin-bottom: 0;
  }

  .nav-bar {
    width: 50%;
    padding: 0 15% 0 0;
  }

  .header-component__text {
    max-width: 400px;
    margin-right: 20px;
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;

  }
}

.about-us-page .header-component {
  margin-bottom: 200px;
}

.about-us-page .header-component__bottom-side::before {
}

@media screen and(max-width: 1920px) {
  .header-component {
    margin-top: 38px;
    margin-bottom: 113px;

    &__left-side{
      margin-left: 18%;
      margin-bottom: 79px;
    }
    &__title {

      font-size: 18px;
      line-height: 39px;

      &--big {
        font-size: 39px;
      }
    }

    .nav-bar {
      padding-right: 10%;
    }

    .header-component__text {
      font-size: 16px;
      line-height: 22px;
    }
  }
  .header-component__bottom-side::before {
    height: 64px;
  }
  .about-us-page .header-component {
    margin-bottom: 150px;
  }
  .about-us-page .header-component__bottom-side::before {
    //bottom: -79px;
  }

}

