@media screen and (max-width: 1200px) {
  .header-component {
    margin-top: 0px;
    background-image: url("../img/main-preview.jpg");
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 100% 76%;
    padding-bottom: 0px;
    margin-bottom: 84px;
    padding-top: 1px;
    &__left-side{
      margin: 0;
    }

    &__title {
      font-size: 16px;
      line-height: 32px;


      &--big {
        font-size: 32px;
      }
    }

    .header-component__bottom-side {
      margin-top: 320px;
      max-width: 644px;
      margin-left: auto;
      margin-right: auto;
      background: rgba(255, 255, 255, 0.7);
      backdrop-filter: blur(25px);
      padding: 50px 75px 0;
      &::before {
        display: none;
      }
    }
    .header-component__img {
      display: none;
    }

    .nav-bar {
      width: auto;
      padding: 0 55px;
      max-width: 760px;
      width: auto;
      margin-top: 0;
    }

    .header-component__text {
      font-size: 16px;
      line-height: 22px;
      margin-top: 20px;
      margin-left: 0;
    }
  }
  .about-us-page .header-component {
    margin-bottom: 0;
  }
  .about-us-page .header-component__bottom-side {
    padding-bottom: 63px;
  }


}


@media screen and (max-width: 768px) {
  .header-component {
    background-image: url("../img/main-preview-m.jpg");
    background-size: 100% 76%;
    margin-bottom: 0;

    &__title {
      font-size: 12px;
      line-height: 21px;

      &--big {
        font-size: 18px;
      }
    }

    .header-component__bottom-side {
      margin-top: 290px;
      max-width: 83.8%;
      padding: 23px 35px 20px;
      top: -30px;
    }

    .nav-bar {
      padding: 0;
      margin-right: 13.5%;
      height: auto;
    }

    .header-component__text {
      font-size: 9px;
      line-height: 11px;
      margin-right: 0;
      margin-top: 20px;
    }

  }
  .about-us-page .header-component__bottom-side {
    padding-bottom: 62px;
  }
}


