.footer{

}

.footer__wrapper{
  position: relative;

  background-image: url("../img/footer-image.jpg");
  background-repeat: no-repeat;
  background-position: right;
  background-size: 1273px 100%;


  &::before{
    content:'';
    position: absolute;

    width: 810px;
    height: 100%;
    right: 0;
    top: 0;

    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(25px);
  }
  .container-min{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.footer__wrapper .card__blur{
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(25px);
}
.form{
  width: 600px;
  padding-right: 172px;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-top: 110px;
}

.form__title{
  font-family: Proxima Nova;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  margin-bottom: 30px;

}


.form__list{
  list-style: none;
  padding: 0;
  margin: 0;

  display: flex;
  flex-direction: column;
  margin-bottom: 60px;
}

.form__input{
  border: none;
  height: 27px;
  width: 100%;

  border-bottom: 0.5px solid #00456D;
  background: transparent;
}
.form__item:not(:last-child){
  margin-bottom: 30px;
}

.form__input,
.form__input::placeholder{
  font-family: Proxima Nova;
  font-size: 18px;
  line-height: 22px;
  color: #00456D;
}
.form__input:focus{
  outline: none;
}
.form__input::placeholder{
  color: rgba(0, 69, 109, 0.5);
}

.form__submit {
  color: white;
  font-family: Proxima Nova;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 29px;
  text-align: center;
  text-transform: uppercase;

  border: none;

  background-color: #FF8767;

  padding: 9px 45px;
}

.footer__info{
  margin: 160px 0 140px 100px;
  z-index: 1;
  font-size: 18px;
  line-height: 22px;
}

.footer__info-list{
  list-style: none;
  padding: 0;
  margin-bottom: 113px;
}

.footer__info-item:not(:last-child){
  margin-bottom: 40px;
}

.footer__info-title{
  margin: 10px 0;

}

.footer__info-text{
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
}

@media screen and (max-width: 1920px) {

  .footer__wrapper{
    background-size: 66.3% 100%;
    &::before{
      width: 42.2%;
    }
  }
  .form{
    width: 52.6%;
    padding-right: 100px;
    padding-top: 45px;
    padding-bottom: 45px;
    margin-top: 83px;
  }
  .form__title{
    font-size: 22px;
    line-height: 25px;
    margin-bottom: 23px;
  }
  .form__list{
    margin-bottom: 45px;
  }

  .form__input{
    height: 21px;
  }
  .form__input:not(:last-child){
    margin-bottom: 23px;
  }
  .form__input,
  .form__input::placeholder{
    font-family: Proxima Nova;
    font-size: 16px;
    line-height: 20px;
  }


  .form__submit {
    padding: 8px 34px;
  }

  .footer__info{
    margin: 120px 0 105px 75px;
  }

  .footer__info-list{
    margin-bottom: 85px;
  }

  .footer__info-item:not(:last-child){
    margin-bottom: 30px;
  }

  .footer__info-text{
    font-size: 22px;
    line-height: 25px;
  }

}

@media screen and (max-width: 1440px) {


  .footer__wrapper{
    //background-size: 793px 100%;
    &::before{
      //width: 505px;
    }
  }
  .form{
    padding-right: 80px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 83px;
  }

  .form__submit {
    padding: 8px 30px;
  }
  .footer__info{
    margin: 120px 0 74px 74px;
  }

  .footer__info-list{
    margin-bottom: 85px;
  }

  .footer__info-item:not(:last-child){
    margin-bottom: 30px;
  }

  .footer__info-text{
    font-size: 22px;
    line-height: 25px;
  }

}
@media screen and (max-width: 1200px) {


  .footer__wrapper .container-min{
    flex-direction: column;
    max-width: 645px;
  }
  .footer__wrapper{
    background-size: 100% 45%;
    background-position: 0% 29%;
  }
  .footer__wrapper::before{
    content: none;
  }
  .form{
    width: 100%;
    padding: 30px 75px;
    margin-top: 83px;
  }

  .form__submit {
    padding: 8px 30px;
  }
  .footer__info{
    width: 100%;
    padding: 30px 75px;
    margin: 120px 0 0 0;

    background: rgba(255,255,255,0.7);
    backdrop-filter: blur(25px);
  }

  .footer__info-list{
    margin-bottom: 85px;
  }

  .footer__info-item:not(:last-child){
    margin-bottom: 30px;
  }

  .footer__info-text{
    font-size: 22px;
    line-height: 25px;
  }

}
@media screen and (max-width: 768px) {


  .footer__wrapper .container-min{
    flex-direction: column;
    max-width: 645px;
  }
  .footer__wrapper{
    background-image: url("../img/footer-image-m.jpg");
    background-size: 100% 45%;
    background-position: 0% 29%;
  }
  .footer__wrapper::before{
    content: none;
  }
  .form{
    max-width: 83.8%;
    padding: 23px 35px 25px;
    margin: 50px auto 0;
  }
  .form__title{
    font-size: 10px;
    line-height: 12px;
    margin-bottom: 10px;
  }

  .form__submit {
    padding: 8px 16px;
    font-size: 12px;
    line-height: 15px;
  }
  .footer__info{
    width: 83.8%;
    padding: 23px 35px 25px;
    margin: 120px auto 0;

    font-size: 12px;
    line-height: 15px;

    background: rgba(255,255,255,0.7);
    backdrop-filter: blur(25px);
  }

  .footer__info-list{
    margin-bottom: 65px;
  }

  .footer__info-item:not(:last-child){
    margin-bottom: 40px;
  }
  .form__item:not(:last-child) {
    margin-bottom: 20px;
  }
  .form__input,
  .form__input::placeholder {
    font-family: Proxima Nova;
    font-size: 9px;
    line-height: 11px;
  }
  .form__list{
    margin-bottom: 20px;

  }
  .footer__info-title{
    margin: 6px 0;
  }
  .footer__info-text{
    font-size: 14px;
    line-height: 17px;
  }

}

