.header{
  padding-top: 1px;
  margin-top: -1px;

  padding-bottom: 111px;
  margin-bottom: 200px;

  position: relative;
}

.header__video{
  position: absolute;
  right: 0;
  top: 0;
  width: 1273px;
  height: 800px;
}
.main-nav{
  display: flex;
  justify-content: space-between;

  margin-top: 55px;
  margin-bottom: 133px;
}


.main-logo{
  width: 242px;
  height: auto;
  margin-left: 162px;
  img{
    width: 100%;
    height: auto;
  }
}
.header__menu-btn{
  display: none;
}
.nav-bar{
  display: flex;

  justify-content: space-between;
  height: 45px;
  align-items: center;

  padding: 0 293px 0 152px;

  max-width: 1112px;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(25px);

  &__right-wrapper{
    display: flex;
    align-items: center;
  }

}

.lang-list{
  list-style: none;

  display: flex;
  padding: 0;
  margin: 0 30px 0 0;
}

.header__card{
  max-width: 600px;
  margin-left: 290px;
  &-blur{
    padding: 54px 122px 55px 0;
  }
  &-text{
    max-width: 91%;
  }
  &-title{
    margin-bottom: 20px;
  }
  &-link{
    margin-left: 26px;
  }

}



@media screen and (max-width: 1920px) {

  .header{
    background-size: 66.3% 100%;

    padding-bottom: 83px;
    margin-bottom: 150px;
  }
  .header__video{
    width:  66.1%;
    object-fit: cover;
    height: 609px;
  }
  .main-nav{
    margin-top: 40px;
    margin-bottom: 100px;

  }
  .main-logo{
    width: 182px;
    margin-left:122px;
  }

  .nav-bar{
    padding: 0 126px 0 85px;
    max-width: 95%;
  }
  .main-page .nav-bar{
    max-width: 58.6%;
  }

  .lang-list{
    margin: 0 23px 0 0;
  }
  .header__card{
    max-width: 31.2%;
    margin-left: 15.2%;
    &-blur{
      padding: 40px 88px 42px 0;
    }
    &-title{
      margin-bottom: 15px;
    }
    &-link{
      margin-left: 26px;
    }
    &-text{
      font-size: 16px;
      line-height: 19px;

    }

  }
}
@media screen and (max-width: 1440px) {

  .header{
    background-size: 66.5% 498px;

    padding-bottom: 67px;
    margin-bottom: 124px;
  }
  .header__video{
    width: 66.5%;
    height: 498px;
  }
  .main-nav{
    margin-top: 15px;
    margin-bottom: 90px;
  }
  .main-logo{
    width: 151px;
    margin-left:101px;
  }

  .nav-bar{
    padding: 0px 55px 0 55px;
    max-width: 760px;
  }

  .lang-list{
    margin: 0 23px 0 0;
  }
  .header__card{
    max-width: 425px;
    margin-left: 180px;
    &-blur{
      margin-bottom: 20px;
      padding: 25px 20px 25px 0;
    }
    &-text{
      max-width: 390px;
    }
    &-title{
      margin-bottom: 15px;
    }
    &-link{
      margin-left: 26px;
    }

  }
}

@media screen and (max-width: 1200px){

  .header{
    background-size: 100% 76%;
    padding-bottom: 0px;
    margin-bottom: 50px;
  }
  .header__video{
    width: 100%;
    height: 76%;
  }
  .main-nav{
    position: fixed;
    z-index: 2;
    top: 0;
    width: 100%;
    left: 50%;
    transform: translate(-50%);
    padding: 11px 0;
    margin: 0 auto;
    max-width: 83.8%;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(25px);
  }
  .main-logo{
    width: 102px;
    margin-left:55px;
  }

  .header__menu-btn{
    background-color: transparent;
    border: none;

    font-size: 20px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: #00456D;

    display: block;
  }

  .nav-bar{
    padding: 0 55px;
    max-width: 760px;
    background: none;
    backdrop-filter: none;
  }

  .lang-list{
    margin: 0;
  }
  .--svg__search{
    display: none;
  }

  .header__card{
    max-width: 644px;
    margin-top: 320px;
    margin-left: auto;
    margin-right: auto;
    &-blur{
      margin-bottom: 40px;
      padding: 50px 75px 0;
    }
    &-text{
      font-size: 12px;
      max-width: 470px;
    }
    &-title{
      margin-bottom: 10px;
    }
    &-link{
      margin-left: 60px;
    }

  }

}

@media screen and (max-width: 768px){

  .header{
  background-size: 100%  66%;
  }
  .header__video{
    height: 66%;

  }
  .main-nav{
    padding: 6px 0;
  }
  .main-logo{
    width: 15.8%;
    margin-left:8.6%;
  }

  .header__menu-btn{
    font-size: 9px;
    line-height: 11px;
    padding: 0;
  }

  .nav-bar{
    padding: 0;
    margin-right: 13.5%;
    height: auto;
  }

  .lang-list{
    font-size: 9px;
    line-height: 11px;
  }

  .header__card{
    max-width: 83.8%;
    margin-top: 200px;
    margin-left: auto;
    margin-right: auto;
    &-blur{
      margin-bottom: 40px;
      padding: 23px 35px 0;
    }
    &-text{
      font-size: 9px;
      line-height: 11px;
      max-width: 470px;
    }
    &-title{
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 10px;
    }
    &-title--small{
      font-size: 12px;
      line-height: 15px;
    }
    &-link{
      font-size: 9px;
      line-height: 29px;
      margin-left: 15px;
    }

  }

}

