.filter-list{
  list-style: none;
  padding: 0;
  margin-bottom: 100px;

  display: flex;
}
.filter-hidden{
  display: none;
}
.filter-item{
  margin-right: 42px;
  font-size: 18px;
  line-height: 22px;
}

.filter-item--active .filter-btn{
  font-weight: 700;
  position: relative;
  &::before{
    content: '';
    width: 5px;
    height: 5px;
    position: absolute;
    background-color: #ff8767;
    left: -10px;
    bottom: 5px;
  }
}
.filter-btn{
  text-decoration: none;
  color: #00456d;
  background: none;
  border: none;
  text-transform: uppercase;
  cursor: pointer;

}

@media screen and (max-width: 1200px) {
  .filter-list{
    margin-bottom: 50px;
  }
  .filter-item{
    margin-right: 3%;
  }
  .filter-btn{
    font-size: 14px;
    line-height: 18px;
  }
}
@media screen and (max-width: 768px) {
  .filter-btn{
    font-size: 9px;
    line-height: 11px;
  }
  .filter-item--active .filter-btn::before{
    bottom: 1px;
    left: -7px;
  }
}
