.section-title__wrapper {
  font-family: Proxima Nova, Serif;
  font-size: 18px;
  line-height: 25px;

  max-width: 540px;
  margin-left: 26.1%;
  margin-bottom: 5.2%;

  position: relative;

}
.section-title__wrapper.--svg__adras-before::before,
.section-title__wrapper.--svg__adras-after::after{
  content: '';
  background-color: #FF8767;
  width: 1000px;
  height: 85px;
  position: absolute;
  top: 0;
}
.section-title__wrapper.--svg__adras-before::before {
  left: -606px;
  transform: translateX(-50%);
}
.section-title__wrapper.--svg__adras-after::after {
  right: -606px;
  transform: translateX(50%);
}

.section-title__header {
  text-transform: uppercase;
  margin-bottom: 20px;

  span {
    display: block;
  }
}

.section-title--small {
  font-weight: 300;
  font-size: 24px;
  line-height: 28px;
}

.section-title--big {
  font-weight: 700;
  font-size: 52px;
  line-height: 52px;
}
@media screen and(max-width: 1920px){
  .section-title__wrapper.--svg__adras-before::before,
  .section-title__wrapper.--svg__adras-after::after{
    height: 64px;
  }
}
@media screen and (max-width: 1440px) {
  .section-title__wrapper {
    font-size: 16px;
    line-height: 20px;
    &::before {
    //  width: 395px;
      height: 60px;
    }
  }

  .section-title__header{
    margin-bottom: 10px;
  }
  .section-title--big {
    font-size: 42px;
    line-height: 42px;
  }
  .section-title__wrapper.--svg__adras-before::before,
  .section-title__wrapper.--svg__adras-after::after{
    height: 53px;
  }

}
@media screen and (max-width: 1200px) {
  .section-title__wrapper {
    font-size: 16px;
    line-height: 20px;
    margin-left: 18%;
  }
  .section-title__wrapper.--svg__adras-before::before {
    left: -530px;
  }
  .section-title__wrapper.--svg__adras-after::after {
    right: -520px;
  }

  .section-title__header{
    margin-bottom: 10px;
  }
  .section-title--big {
    font-size: 36px;
    line-height: 36px;
  }
  .section-title--small{
    font-size: 18px;
  }
  .section-title__wrapper.--svg__adras-before::before,
  .section-title__wrapper.--svg__adras-after::after{
    height: 53px;
  }

}

@media screen and (max-width: 768px) {
  .section-title__wrapper {
    max-width: 70%;
    font-size: 12px;
    line-height: 20px;
  }
  .section-title__wrapper.--svg__adras-before::before {
    height: 60px;
    left: -440px;
    top: 0;
  }
  .section-title__wrapper.--svg__adras-after::after {
    right: -540px;
  }
  .section-title__header{
    margin-bottom: 10px;
    margin-left: 70px;
  }
  .section-title--big {
    font-size: 18px;
    line-height: 22px;
  }
  .section-title--small{
    font-size: 12px;
    line-height: 15px;
  }
  .section-title__text{
    max-width: 360px;
  }
  .section-title__wrapper.--svg__adras-before::before,
  .section-title__wrapper.--svg__adras-after::after{
    height: 46px;
  }

}
