.vendor-info {
  &__wrapper {
    background-position: left top;
    background-repeat: no-repeat;
    background-size: 960px 100%;
    margin-bottom: 150px;
    padding-top: -1px;
    margin-top: -1px;
    padding-bottom: 100px;
  }
}
.about-vendor-page--samsung .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-samsung.jpg");
}
.about-vendor-page--canon .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-canon.jpg");
}
.about-vendor-page--acer .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-acer.jpg");
}
.about-vendor-page--axis .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-axis.jpg");
}
.about-vendor-page--cisco .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-cisco.jpg");
}
.about-vendor-page--lenovo .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-lenovo.jpg");
}
.about-vendor-page--nec .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-nec.jpg");
}
.about-vendor-page--panasonic .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-panasonic.jpg");
}
.about-vendor-page--realme .vendor-info__wrapper{
  background-image: url("../img/about-vendors/about-vendor-realme.jpg");
}
.vendor-info__card {
  width: 850px;
  margin-left: auto;
  margin-right: 470px;

  margin-top: 0;

  padding: 60px 0 60px 180px;

  &-title {
    width: 63.5%;
    margin-bottom: 50px;
  }

  &-text {
    margin-bottom: 100px;
  }
}
.links{
  margin-bottom: 200px;
  &__nav-list{
    list-style: none;
    padding: 0;
    margin: 0;

    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-bottom: 100px;
  }
  &__nav-item:not(:last-child){
    margin-right: 50px;
    margin-bottom: 50px;
  }
  &__nav-link{
    font: inherit;
    color: inherit;
    text-decoration: none;
    white-space: nowrap;
  }
  &__nav-item--active{
    font-weight: 700;
  }

  &__list{
    list-style: none;
    padding: 0;
    margin: 0 auto;

    width: 660px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    width: 50%;
  }
  &__item:not(:nth-last-child(-n +2)){
    margin-bottom: 60px;
  }
}




.video{
  margin-bottom: 200px;
  &__list{
    list-style: none;
    padding: 0;
    margin: 0;

    display: flex;
    justify-content: space-between;

  }

  &__item{
    position: relative;
    width: 45.8%;
    &::before{
      content: '';
      width: 150px;
      height: 5px;
      background-color:#00456D;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  &__img{
    width: 100%;
    margin-bottom: 40px;
  }
  &__time{
    font-weight: 700;
    line-height: 22px;

    color: #FF8767;

    display: block;

    margin-bottom: 25px;
  }
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    width: 400px;
  }
  &__text{
    width: 400px;
    margin-bottom: 100px;
  }

}

