.products {
  margin-bottom: 100px;
}

.products__list {
  list-style: none;
  padding: 0;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.products__item {
  width: 30%;
  display: flex;
  flex-direction: column;

  margin-bottom: 100px;
}

.products__item:last-child:nth-child(3n - 1) {
  margin-right: auto;
  margin-left: 4.8%;
}

.products__item-top {
  display: flex;
  margin-bottom: 20px;
}

.products__svg {
  margin-right: 20px;
  height: auto;
  width: 22%;
}

.products__item-title {
  font-size: 24px;
  text-transform: uppercase;
  margin-top: auto;
}

.products__item-title--small {
  display: block;
  margin-bottom: 11px;
  font-weight: 400;
}

.products__item-title--big {
  display: block;
}

.products__item-text {
  margin-bottom: 50px;
}

.products__item-bottom {
  display: flex;
  justify-content: flex-start;
  margin-top: auto;
}

.products__more {
  background-color: #FF8767;
  color: #ffffff;
  margin-right: 20px;
}

.products__get {
  background-color: #ffffff;
  color: #ff8767;
}


@media screen and (max-width: 1920px) {
  .products {
    margin-bottom: 100px;
  }
  .products .container-min {
    padding: 0 40px;
  }
  .products__item {
    font-size: 16px;
    width: 32%;
    margin-bottom: 75px;
  }
  .products__item-top {
    margin-bottom: 15px;
  }
  .products__svg {
    margin-right: 15px;
  }
  .products__item-title {
    font-size: 18px;
    line-height: 20px;
  }
  .products__item-text {
    margin-bottom: 38px;
  }
  .btn {
    padding: 7px 33px;
  }
}

@media screen and (max-width: 1200px) {
  .products {
    margin-bottom: 25px;
  }
  .products__item {
    margin-bottom: 75px;
    width: 45%;
  }
  .products__item:last-child:nth-child(2n + 1) {
    margin: 0 auto 75px;
  }
}

@media screen and (max-width: 768px) {
  .products {
    margin-bottom: 30px;
  }
  .products__item {
    width: 100%;
    margin-bottom: 50px;
  }
  .products__item:last-child {
    margin-right: auto;
    margin-left: auto;
  }
  .products__item-text {
    max-width: 480px;
  }
  .products__item-bottom {
    flex-direction: column;
  }
  .products__more {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .products__more,
  .products__get {
    width: 72%;
  }
}


