.news{
  margin-bottom: 200px;
}
.news__list {
  list-style: none;
  padding: 0;
}
.news__item {
  display: flex;
  position: relative;
}
.news__item:not(:last-child) {
  margin-bottom: 100px;
}
.news__wrapper {
  position: relative;
  flex-grow: 1;
  padding-bottom: 50px;
  width: 50%;
  .card__blur {
    height: 100%;
    margin-bottom: 5px;
  }
}
.news picture {
  width: 50%;
}
.news picture img{
  width: 100%;
  height: 100%;
}

// нечетные
.news__item:nth-child(odd) {
  animation-name:slideInLeft;
  .news__wrapper {
    left: -130px;
  }
  .card__blur {
    padding-left: 50px;
  }
  .news__link {
    left: 180px;
  }
}

// четные
.news__item:nth-child(even) {
  animation-name:slideInRight;
  img {
    margin-left: auto;
  }

  .news__wrapper {
    order: -1;
    right: -130px;
    text-align: right;
  }

  .card__blur {
    padding-right: 50px;
  }

  .news__title,
  .news__sub-title {
    margin-left: auto;
  }

  .news__link {
    right: 180px;
  }

}

.news__date {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;

  margin-bottom: 10px;
}

.news__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;

  width: 400px;

  margin-bottom: 10px;
}

.news__sub-title {
  font-size: 14px;
  line-height: 17px;
  width: 360px;
}

.news__link {
  position: absolute;
}


@media screen and (max-width: 1920px) {
  .news {
    margin-bottom: 150px;
  }
  .news__item:not(:last-child) {
    margin-bottom: 75px;
  }
  // нечетные
  .news__item:nth-child(odd) {
    .news__wrapper {
      left: -11%;
    }
    .card__blur {
      padding-left: 38px;
    }

    .news__link {
      left: 26%;
    }
  }

  // четные
  .news__item:nth-child(even) {
    .news__wrapper {
      right: -11%;
    }
    .card__blur {
      padding-right: 38px;
    }
    .news__link {
      right: 26%;
    }

  }

  .news__date {
    font-size: 18px;
    line-height: 22px;
  }

  .news__title {
    font-size: 18px;
    line-height: 22px;

    width: 80%;
    margin-bottom: 10px;
  }
  .news__sub-title {
    font-size: 14px;
    line-height: 17px;

    width: 85%;
  }
}
@media screen and (max-width: 1440px) {
  .news {
    margin-bottom: 125px;
  }
  .news__item {
    .card__blur {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  .news__item:not(:last-child) {
    margin-bottom: 62px;
  }

  // нечетные
  .news__item:nth-child(odd) {
    .card__blur {
      padding-left: 32px;
    }
  }

  // четные
  .news__item:nth-child(even) {
    .card__blur {
      padding-right: 32px;
    }
  }
}

@media screen and (max-width: 1200px){
  .news .container-min{
    max-width: initial;
  }
  .news{
    margin-bottom: 80px;
  }
  .news__date {
    font-size: 14px;
    line-height: 18px;
  }

  .news__title {
    font-size: 14px;
    line-height: 18px;

    margin-bottom: 10px;
  }
  .news__sub-title {
    font-size: 12px;
    line-height: 12px;

  }
}
@media screen and (max-width: 768px){
  .news__wrapper{
    padding-bottom: 20px;
  }
  .news__item:nth-child(odd) {
    .card__blur {
      padding-left: 22px;
    }
  }

  // четные
  .news__item:nth-child(even) {
    .card__blur {
      padding-right: 22px;
    }
  }
  .news__date {
    font-size: 9px;
    line-height: 11px;
  }
  .news__title {
    font-size: 10px;
    line-height: 12px;
    width:100%;
    margin-bottom: 10px;
  }
  .news__sub-title {
    width: 100%;
    font-size: 9px;
    line-height: 11px;
  }
}
