.breadcrumbs-list {
  list-style: none;
  margin-left: auto;
  display: flex;
}
.breadcrumbs-item a{
  font-size: 18px;
  line-height: 22px;
}
.breadcrumbs-item:not(:last-child) {
  margin-right: 22px;
}

.--svg__next-icon-before,
.--svg__prev-icon-before {
  position: relative;
}

.--svg__next-icon-before::before {
  position: absolute;
  width: 65px;
  height: 44px;
  left: 54%;
  top: 50%;
  transform: translate(-26%,-50%);
  background-size: 100%;
  background-repeat: no-repeat;
}

.--svg__prev-icon-before::before{
  position: absolute;
  top: 38%;
  left: 0;
  transform: translate(0,-50%);
  width: 20px;
  height: 8px;
  background-size: 100%;
  background-repeat: no-repeat;
}

.breadcrumbs-number {
  color: rgba(0, 69, 109, 0.5);
}
.breadcrumbs-item-prev a{
  font-size: 0  ;
  width: 20px;
  height: 20px;
}

