.about-us {
  &__wrapper {
    background-image: url("../img/about-us.jpg");
    background-position: left top;
    background-repeat: no-repeat;
    background-size: 960px 100%;
    margin-bottom: 200px;
    padding-top: 1px;
    margin-top: -1px;
    padding-bottom: 125px;
  }
}
.about-us__card {
  width: 1273px;

  margin-left: auto;
  margin-top: 125px;

  padding: 60px 0 0 160px;

  &::after{
    background-color: #ff8767;
    width: 1000px;
    height: 130px;
    position: absolute;
    top: 0;
    left: 616px;
  }

  &-title {
    margin-bottom: 50px;
  }

  &-text--top {
    margin-bottom: 50px;
    max-width: 460px;
  }

  &-text--bottom {
    max-width: 470px;
    margin-left: auto;
    margin-right: 405px;
  }
}

.advantages__list {
  list-style: none;
  padding: 0;
  margin-bottom: 100px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}

.advantages__item {
  color: #003056;

  font-weight: 400;
  font-size: 24px;
  line-height: 24px;

  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 100px;
  margin-right: 30px;

  svg {
    display: block;
    margin-right: 40px;
  }
}

.advantages__item:nth-child(1) {
  width: 30.3%;
}

.advantages__item:nth-child(2) {
  width: 31.6%;
}

.advantages__item:nth-child(3) {
  width: 21.4%;
}

.advantages__item:nth-child(4) {
  width: 41.9%;
}

.advantages__item:nth-child(5) {
  width: 37.1%;
}

.advantages__item:nth-child(3),
.advantages__item:nth-child(5) {
  margin-right: 0;
}


.advantages__text-wrapper span {
  display: block;
}

.advantages__numbers {
  font-weight: 700;
  font-size: 120px;
  line-height: 120px;
  text-transform: uppercase;
}

// svg размеры
.advantages__list svg{
  width: 100%;
}

.svg-container--develop {
  width: 32.4%;
}

.svg-container--briefcase {
  width: 34.7%;
}

.svg-container--worldwide {
  width: 45.9%;
}

.svg-container--handshake {
  width: 25.2%;
}

.svg-container--employee {
  width: 28.2%;
}


@media screen and (max-width: 1920px) {
  .about-us {
    &__wrapper {
      background-size: 50% 100%;
      margin-bottom: 150px;
      padding-bottom: 94px;
    }
  }
  .about-us__card {
    width: 66.3%;
    margin-top: 95px;
    padding: 45px 0 0 90px;

    &::after{
      height: 110px;
      left: 434px;
    }
    &-title {
      margin-bottom: 38px;
    }

    &-text--top {
      margin-bottom: 38px;
      max-width: 44%;
    }

    &-text--bottom {
      max-width: 45%;
      margin-right: 30%;
    }
  }

  .advantages__list {
    margin-bottom: 75px;
  }

  .advantages__item {
    font-size: 18px;
    line-height: 18px;
    margin-bottom: 75px;
  }

  .advantages__numbers {
    font-size: 90px;
    line-height: 90px;
  }
}

@media screen and (max-width: 1440px) {
  .about-us {
    &__wrapper {
      margin-bottom: 108px;
      padding-bottom: 78px;
    }
  }
  .about-us__card {
    margin-top: 80px;
    padding: 35px 0 0 75px;
    &::after{
      height: 85px;
      left: 378px;
    }

    &-title {
      margin-bottom: 25px;
    }

    &-text--top {
      margin-bottom: 25px;
    }
  }

  .advantages__list {
    margin-bottom: 62px;
  }

  .advantages__item {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 62px;
  }

  .advantages__numbers {
    font-size: 75px;
    line-height: 75px;
  }
}

@media screen and(max-width: 1200px) {
  .about-us {
    margin-top: 145px;
  }
  .about-us .container-min{
    max-width: 635px;
  }
  .about-us__wrapper {
    margin-top: 150px;
    background-size: 100% 561px;
    margin-bottom: 80px;
  }
  .about-us__card {
    position: relative;
    top: -246px;
    width: 644px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 94px;
    padding: 20px 75px 75px;
    &::after{
      height: 65px;
      left: 313px;
    }
  }
  .about-us__card-text--top {
    max-width: 80%;
    margin-bottom: 10px;
  }
  .about-us__card-text--bottom {
    max-width: 80%;
    margin-left: 0;
    margin-right: 10%;

  }
  .advantages__list{
    position: relative;
    top: -80px;
    padding: 0 75px;
    background: rgba(255,255,255,.5);
    backdrop-filter: blur(25px);
    margin-bottom: 0;
  }

  .advantages__list .advantages__item {
    width: 40%;
  }
  .advantages__item:not(:last-child){
    margin-bottom: 30px;
  }
  .advantages__item:last-child{
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .advantages__numbers {
    font-size: 48px;
    line-height: 58px;
  }
  .advantages__item>span{
    width: 35%;
  }

  .svg-container--develop {
    width: 32.4%;
  }

  .svg-container--briefcase {
    width: 34.7%;
  }

  .svg-container--worldwide {
    width: 45.9%;
  }

  .svg-container--handshake {
    width: 25.2%;
  }

  .svg-container--employee {
    width: 28.2%;
  }
}

@media screen and(max-width: 768px){
  .about-us{
    margin-top: 215px;
  }
  .about-us .container-min{
    max-width: 83.8%;
  }
  .about-us__wrapper {
    background-image: url("../img/about-us-m.jpg");
    background-size: 100% 71.1%;
    margin-top: 40px;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .about-us__card {
    max-width: 83.8%;
    padding: 23px 35px 36px;
    &::after{
      height: 53px;
      left: 200px;
    }
  }
  .about-us__card-text--top {
    max-width: 360px;
  }
  .about-us__card-text--bottom {
    max-width: 360px;
  }
  .advantages__list{
    padding: 15px 35px 0;
    top: -150px;
  }

  .advantages__list .advantages__item {
    width: 100%;
    justify-content: flex-start;
    margin-right: 0;
    margin-left: 0;
  }
  .advantages__item:nth-child(4)>span{
    margin-right: 18px;
  }
  .advantages__item:not(:last-child){
    margin-bottom: 50px;
  }
  .advantages__text{
    font-size: 18px;
    line-height: 22px;
  }
  .advantages__item>span{
    margin-right: 30px;
  }
}





