.lead{
  overflow: hidden;
}
.lead .section-title__wrapper{
  margin-bottom: 150px;
}
.lead .section-title__header{
  text-align: right;
}
.lead__list{
  list-style: none;
  padding: 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;


  margin-bottom: 200px;
}

.lead__item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 26.7%;

  font-weight: normal;
  line-height: 22px;
}

.lead__item:first-child{
  margin-right: auto;
}
.lead__item:nth-child(3){
  margin-left: auto;
}
.lead__item:not(:nth-last-child(-n+2)){
  margin-bottom: 72px;
}

.lead__item-title{
  margin-top: auto;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  margin-bottom: 14px;
}
.lead__item:nth-child(4) .lead__item-title{
  margin-top: 7px;
}
.lead__item-text{
  width: 318px;
}
.lead__svg{
  display: block;
  height: auto;
  margin-bottom: 20px;
}
.lead__svg--team{
  width: 32%;
}
.lead__svg--human_relations{
  width: 37%;
}
.lead__svg--competence{
  width: 32%;
}
.lead__svg--efficiency{
  width: 30%;
}
.lead__svg--safety{
  width: 32%;
}
.lead__svg--responsibility{
  width: 33%;
}

@media screen and(max-width: 1920px) {
  .lead .section-title__wrapper{
    margin-bottom: 113px;
  }
  .lead__list{
    margin-bottom: 150px;
  }

  .lead__item{
    //width: 240px;
  }
  .lead__item:not(:nth-last-child(-n+2)){
    margin-bottom: 54px;

  }

  .lead__item-title{
    font-size: 18px;
    line-height: 24px;
    max-width: 165px;
    margin-bottom: 14px;
  }
  .lead__item-text{
    width: 238px;
  }
}

@media screen and(max-width: 1200px) {
  .lead .section-title__wrapper{
    margin-bottom: 113px;
  }
  .lead__list{
    margin-bottom: 80px;
    justify-content: space-between;
  }


  .lead .lead__item{
    width: 40%;
    margin-right: 0;
    margin-left: 0;
  }
  .lead__item:not(:last-child){
    margin-bottom: 54px;
  }
  .lead__item:last-child{
    margin-right: auto;
    margin-left: auto;
  }
  .lead__svg{
    margin-bottom: 10px;
  }
  .lead__item-title{
    font-size: 16px;
    line-height: 20px;
    max-width: 100%;
    margin-bottom: 14px;
  }
  .lead__item-text{
    font-size: 12px;
    line-height: 15px;
    width: 100%;
  }
}

@media screen and(max-width: 1200px) {
  .lead .section-title__wrapper{
    margin-bottom: 80px;
    margin-left: 0;
    max-width: 59%;
  }
  .lead__list{
    margin-bottom: 80px;
    justify-content: space-between;
  }


  .lead .lead__item{
    width: 45%;
    margin-right: 0;
    margin-left: 0;
    align-items: center;
    text-align: center;

  }
  .lead__item:not(:last-child){
    margin-bottom: 54px;
  }
  .lead__item:last-child{
    margin-right: auto;
    margin-left: auto;
  }
  .lead__svg{
    margin-bottom: 10px;
  }
  .lead__item-title{
    margin-top: auto;
    font-size: 18px;
    line-height: 20px;
    max-width: 100%;
    margin-bottom: 14px;
  }

  .lead__item-text{
    font-size: 12px;
    line-height: 15px;
    width: 100%;
  }
}
@media screen and(max-width: 768px) {

  .lead .lead__item{
    width: 100%;
  }
}


