.link{
  font-family: Proxima Nova, Serif;
  font-size: 14px;
  line-height: 29px;;
  text-decoration: none;
  text-transform: uppercase;

  color: #00456d;

  background-color: white;
  display: inline-block;
}
.--svg__link-icon-before {
  position: relative;
  padding-left: 77px;
}
.--svg__download-icon-before{
  position: relative;
  padding-left:56px;
}

.--svg__link-icon-before::before,
.--svg__link-icon-after::after{
  content: '';
  position: absolute;
  width: 62px;
  height: 46px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-size: 100%;
  background-repeat: no-repeat;
  transition: left 500ms ;

}
.products-item-link::before{
  transform: rotateY(180deg) translateY(-50%);
}

.--svg__link-icon-before:hover::before,
.--svg__link-icon-after:hover::after{
  left: 7px;
}
.products-item-link:hover::before{
  left: -7px;
}
.--svg__download-icon-before::before,
.--svg__download-icon-after::after{
  content: '';
  position: absolute;
  width: 41px;
  height: 46px;
  left: 0;
  bottom: 0;
  background-size: 100%;
  background-repeat: no-repeat;

}

  @media screen and(max-width: 1920px){
    .--svg__link-icon-after::after,
    .--svg__link-icon-before::before {
      width: 45px;
      height: 34px;

  }
    .--svg__link-icon-before:hover::before,
    .--svg__link-icon-after:hover::after{
      left: 26px;
    }
    .products-item-link:hover::before{
      left: -16px;
    }

}

@media screen and (max-width: 1440px){
  .link{
    line-height: 22px;;
  }
  .--svg__link-icon-before{
    padding-left: 80px
  }
  .--svg__download-icon-before::before,
  .--svg__download-icon-after::after{
    width: 35px;
    height: 38px;
  }
}
@media screen and (max-width: 768px){
  .link{
    font-size: 9px;
    line-height: 11px;
  }
  .--svg__link-icon-before::before,
  .--svg__link-icon-after::after{
    width: 31px;
    height: 23px;

  }
  .--svg__link-icon-before:hover::before,
  .--svg__link-icon-after:hover::after{
    left: 28px;
  }
  .products-item-link:hover::before{
    left: -14px;
  }
  .--svg__download-icon-before{
    padding: 42px 0 0 0;
  }
  .--svg__download-icon,
  .--svg__download-icon-before::before,
  .--svg__download-icon-after::after{
    left: 0;
    top: 0;
    width: 31px;
    height: 32px;
  }
}

