.card{
  &__blur{
    padding-top: 55px;
    padding-bottom: 55px;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(25px);
  }
  &__title{
    margin: 0;
    font-size: 52px;
    line-height: 52px;
    text-transform: uppercase;
    margin-bottom: 20px;
    &--small{
      font-weight: 300;
      font-size: 18px;
      line-height: 25px;
      display: block;
    }
  }
}

@media screen and (max-width: 1920px) {
  .card{
    &__blur{
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &__title{
      font-size: 42px;
      line-height: 42px;
      margin-bottom: 15px;
      &--small{
        font-weight: 300;
        font-size: 16px;
        line-height: 23px;
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .card{
    &__blur{
      padding-top: 25px;
      padding-bottom: 25px;
    }
    &__title{
      font-size: 35px;
      line-height: 35px;
      margin-bottom: 10px;
      &--small{
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .card{
    &__blur{
      padding-top: 25px;
      padding-bottom: 25px;
    }
    &__title{
      font-size: 22px;
      line-height: 35px;
      margin-bottom: 10px;
      &--small{
        font-weight: 300;
        font-size: 14px;
        line-height: 20px;
      }
    }
    &__text{
      font-size: 12px;
    }
  }
}
@media screen and (max-width: 768px) {
  .card{
    &__blur{
      padding-top: 25px;
      padding-bottom: 25px;
    }
    &__title{
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 10px;
      &--small{
        font-size: 12px;
        line-height: 15px;
      }
    }
    &__text{
      font-size: 9px;
      line-height: 11px;
    }
  }
}
